export const sendDataToServer = async (send_json) => {
    try {
        const {data} = await axios.post(`/api/send_nft/`, send_json)
        if (data.status) {
            return data.status
        }
        else {
            return "error"
        }

       
    } catch (error) {
         if (error.response && error.response.data) {
            const status = error.response.data.status;
            console.log(status)
            return status
        }
        console.log(error)
        return "error"
    }
}

