export const getUserInfo = async (telegram_id) => {
    const sending_json = {
        telegram_id: telegram_id
    }
    try {
        const {data} = await axios.post(`/api/user_info/`, sending_json)
        if (data.status === "success") {
            return data.user_info
        }
        console.log("Error:",data.description);
        

       
    } catch (error) {        
        console.log(error)
        
    }

}

export const addVisitedChannel = async (wallet_address, channel) => {
    const sending_json = {
        wallet_address: wallet_address,
        channel: channel
    }
    try {
        const {data} = await axios.post(`/api/add_visited_channel/`, sending_json)
        if (data.status === "success") {
            return true
        }
        return false

       
    } catch (error) {
        console.log(error)
    }

}

